import React from 'react';
import './Home.scss';
import ambadi1 from '../assets/ambadi1.jpeg';
import ambadi2 from '../assets/ambadi2.jpeg';
import ambadi3 from '../assets/ambadi3.jpeg';
import ambadi4 from '../assets/ambadi4.jpeg';
import ambadi5 from '../assets/ambadi5.jpeg';
import ambadi6 from '../assets/ambadi6.jpeg';
import ambadi7 from '../assets/ambadi7.jpeg';
import ambadi8 from '../assets/ambadi8.jpeg';
import ambadi9 from '../assets/ambadi9.jpeg';
import ambadi10 from '../assets/ambadi10.jpeg';
import ambadi11 from '../assets/ambadi11.jpeg';
import ambadi12 from '../assets/ambadi12.jpeg';
import ambadi13 from '../assets/ambadi13.jpeg';
import ambadi14 from '../assets/ambadi1.JPG';
import ambadi15 from '../assets/ambadi2.jpg';
import ambadi16 from '../assets/ambadi4.jpg';
class Home extends React.Component {
  constructor(props){
    super(props);
    this.state = {
        imagesList:[{
            imgSrc:ambadi1,
            alt:"ambadi1",
            visible: true
        },{
            imgSrc:ambadi2,
            alt:"ambadi2",
            visible: false
        },{
          imgSrc:ambadi3,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi4,
          alt:"ambadi2",
          visible: false
        },{
            imgSrc:ambadi5,
            alt:"ambadi2",
            visible: false
        },{
          imgSrc:ambadi6,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi14,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi15,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi16,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi7,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi8,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi9,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi10,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi11,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi12,
          alt:"ambadi2",
          visible: false
        },{
          imgSrc:ambadi13,
          alt:"ambadi2",
          visible: false
        }],
        activeItem: 0,
        index:0
    }
  }
  addOne = ()=>{
      let activeItem = this.state.activeItem;
      let activeItemTemp = this.state.activeItem;
      let imagesList = this.state.imagesList;
      if(activeItem===imagesList.length-1){
        for(let i=0;i<imagesList.length;i++){
                    if(i === 0){
                        imagesList[i].visible = true;
                        activeItemTemp = 0;
                    }else{
                        imagesList[i].visible = false;
                    }
        }
      }else{
        for(let i=0;i<imagesList.length;i++){
                    if(i === activeItem+1){
                        imagesList[i].visible = true;
                        activeItemTemp = activeItemTemp + 1;
                    }else{
                        imagesList[i].visible = false;
                    }
        }
        }
      this.setState({
        imagesList:imagesList,
        activeItem:activeItemTemp
      })
  }
  scrollLeft(){
      var checkIfEnd;
      setInterval(function(){ 
          let elmnt = document.getElementById("designRow");
          elmnt.scrollLeft += 10;
          if((checkIfEnd===elmnt.scrollLeft) && elmnt.scrollLeft!==0){
              elmnt.scrollLeft = 0;
          }
          checkIfEnd = elmnt.scrollLeft;
       }, 500);
  }
  componentDidMount(){
    this.scrollLeft();
  }
  render(){
  return (
      <div className="photoAlbum row px-0">
          <div className="balooons">
            <span className="baloon1"></span>
            <span className="baloon2"></span>
          </div>
          <div className="birthday">
            <div></div>
          </div>
          <div className="animals">
            <div>
              <div className="msg">
                <span className="our">Our</span>
                <span className="name">Ambadi</span>
                <span className="turn">is Turning</span>
              </div>
            </div>
          </div>
          <div className="locateUs">
            <a href="https://goo.gl/maps/yrgxEEe8Cjaij6rp8" target="_blank"></a>
          </div>
          <div className="col-12 col-md-7 imagesPlot p-0"><div className="anim" >
            <div id="designRow">
              {this.state.imagesList.map((val,key)=>(
              <div style={{backgroundImage: `url('${val.imgSrc}')`}} key={key}>
              </div>))}
            </div>
            <span></span>
          </div>
          </div>
        <div class="marquee">
          <p><span>Join us on</span>&nbsp;<span>8th April 2021</span>&nbsp;<span>(4pm - 8pm)</span>
          </p></div>
      </div>
    )
  }
}

export default Home;
